.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-body h3 {
  font-style: bold;
}
ul.list-ul {
  margin: 0px;
  padding: 0px;
}

.li-list {
  list-style: none;
  border: solid 1px #ccc;
  padding: 10px;
  margin-bottom: 25px;
}

.amount{
  background-color: gainsboro;
  height: auto;
  width: 1000px;
  margin: auto;
}

.amount button{
  margin-bottom: 20px;
}